











































































































































import Vue from "vue";
import Axios from "axios";
import { mdiChip } from "@/assets/svg";

const axios = Axios.create({
  baseURL: `http://${window.location.host}`,
  timeout: 10000,
});

export default Vue.extend({
  name: "HelloWorld",

  computed: {
    buttonEnabled() {
      return this.ssid.length && this.password.length > 7;
    },
  },
  data: () => ({
    icons: {
      chip: mdiChip as string,
    },
    ssid: "",
    password: "",
    mqttIp: [192, 168, 0, 0],
    mqttLogin: "",
    mqttPassword: "",
    mqttTopic: "weathermonitor",
    mqttLoading: false,
    loading: false,
    showError: false,
    errorMessage: "An error occured, please try again",
    color: "warning",
  }),
  methods: {
    async updateFlash() {
      try {
        this.loading = true;
        await axios.post(
          "update",
          `ssid=${this.ssid}&password=${this.password}`
        );
        this.errorMessage =
          "WiFi Configured! please rejoin your usual WiFi Connection and reboot your weather monitor";
        this.color = "success";
        this.showError = true;
        setTimeout(() => {
          this.showError = false;
        }, 10000);
        this.loading = false;
      } catch (error) {
        this.errorMessage = "An error occured, please try again";
        this.showError = true;
        this.loading = false;
        setTimeout(() => {
          this.showError = false;
        }, 3000);
      }
    },
    async updateMqtt() {
      // TBD
    },
  },
  async beforeMount() {
    try {
      const configObject = await axios.get("config");
      const config = configObject.data;
      this.ssid = config.ssid;
      this.password = config.password;
      this.mqttIp = config.mqttIp;
      this.mqttTopic = config.mqttTopic;
      this.mqttPassword = config.mqttPassword;
    } catch (error) {
      console.error(error);
    }
    // Trying to get the current config from the server
  },
});
